<template>
	<view class="board">
		<view class="box">
			<view class="title">项目列表</view>
			<searchBox :showProject="true" :showRange="true" :timeStr="timeStr" @handleSearch="handleSearch">
			</searchBox>
			<view class="table_wrap">
				<view class="tabs flex flex_start">
					<view class="tab" v-for="(item,index) in tabs" :class="currentTab.value==item.value?'active':''"
						@click="changeTab(item)">{{item.text}}</view>
				</view>
				<uni-table border stripe emptyText="暂无更多数据" style="height:500px;">
					<uni-tr style="background: #f6f6f6;">
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">项目编号</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">项目名称</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">工人薪资</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">在职人数</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">项目状态</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">创建时间</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">操作</uni-th>
					</uni-tr>
					<uni-tr v-for="(item,index) in list" :key="index">
						<uni-td align="center">{{item.code}}</uni-td>
						<uni-td align="center">{{item.name}}</uni-td>
						<uni-td align="center">{{item.worker_salary}}</uni-td>
						<uni-td align="center">{{item.on_job_worker_count}}</uni-td>
						<uni-td align="center">
							<view class="status">{{status.filter(el => {return el.value == item.status})[0].text}}
							</view>
							<view class="reason" style="font-size: 12px;color:#f00;"
								v-if="item.status == 'platform_review_rejected'">{{item.platform_review_remark}}</view>
						</uni-td>
						<uni-td align="center">{{item.create_time}}</uni-td>
						<uni-td align="center" style="padding:8px 0px;">
							<view class="ope flex flex_around">
								<view class="ope_item" @click="creatCode(item)" v-if="item.status == 'running'">招人
								</view>
								<view class="ope_item" v-if="item.status == 'platform_reviewing'"
									@click="toHandle(item,'running')">
									通过</view>
								<view class="ope_item" v-if="item.status == 'platform_reviewing'"
									@click="openReason(item)">拒绝</view>
								<view class="ope_item" @click="endProject(item)" v-if="item.status == 'running'">完结
								</view>
								<view class="ope_item" @click="navigate({pageName:'project_detail'},item.id)">详情</view>
							</view>
						</uni-td>
					</uni-tr>
				</uni-table>
				<pageBox :page="pagination" @toNext="toNext"></pageBox>
			</view>
		</view>
		<view class="mask" v-if="showMask">
			<view class="inner" v-if="showReason" style="padding:0;">
				<view class="title">拒绝理由（{{currentProject.name}}）</view>
				<view class="close" @click="close">
					<image src="/static/close_white.png" mode="widthFix"></image>
				</view>
				<view class="box" style="padding:20px 20px 0 20px;">
					<view class="line flex flex-start">
						<view class="tit">理由：</view>
						<view class="input_wrap">
							<input type="text" v-model="refuseReason" placeholder="请输入拒绝理由,不超过20个字" maxlength="20" />
						</view>
					</view>
					<view class="btns flex flex_end">
						<view class="btn save" @click="toHandle(currentProject,'platform_review_rejected')">确认拒绝</view>
					</view>
				</view>
			</view>
			<view class="inner" v-if="showHaibao">
				<view class="btns flex flex_end">
					<view class="btn close" @click="close">关闭</view>
					<view class="btn save" @click="save">保存</view>
				</view>
				<l-painter ref="painter" :board="poster" />

			</view>
		</view>
	</view>
</template>

<script>
	import searchBox from "@/components/search_box.vue"
	import pageBox from "@/components/paging_box.vue"
	import commonData from "@/common/commonData"
	import {
		saveAs
	} from 'file-saver'
	const app = getApp()
	export default {
		name: "contract_list",
		data() {
			return {
				refuseReason: "",
				// pageIndex:1,
				timeStr: "立项",
				path: "",
				poster: {
					css: {
						// 根节点若无尺寸，自动获取父级节点
						width: '500px',
					},
					views: []
				},
				periodList: [{
						value: "hour",
						text: "时"
					},
					{
						value: "day",
						text: "天"
					},
					{
						value: "week",
						text: "周"
					},
					{
						value: "month",
						text: "月"
					},
				],
				list: [
					// {
					// 	id:"3546436",
					// 	code:"123432",
					// 	name:"富士康招技工",
					// 	salary_unit_price:"5000-6000元/月",
					// 	worker_num:30,
					// 	status:"reviewing",
					// 	create_time:"2024-04-15 11:30:05"
					// },
					// {
					// 	id:"3546436",
					// 	code:"123432",
					// 	name:"富士康招技工41111",
					// 	salary_unit_price:"5000-6000元/月",
					// 	worker_num:30,
					// 	status:"running",
					// 	create_time:"2024-04-15 11:30:05"
					// }
				],
				currentTab: {
					value: "all",
					text: "全部"
				},
				tabs: [{
						value: "all",
						text: "全部"
					},
					{
						value: "reviewing",
						text: "待审核"
					},
					{
						value: "running",
						text: "进行中"
					}, {
						value: "finished",
						text: "已完结"
					}
				],
				status: commonData.projectStatus,
				showMask: false,
				showHaibao: false,
				showReason: false,
				currentId: "",
				currentMoreId: 0,
				years: "",
				continueFee: "",
				searPro: "",
				searStartTime: "",
				searEndTime: "",
				currentPage: 1,
				pagination: {},
				commissionValue: 0,
				currentProject: {}
			};
		},
		components: {
			searchBox,
			pageBox
		},
		created() {
			let paramArr = []
			if (location.href.indexOf("?") != -1) {
				paramArr = location.href.split("?")[1].split("&")
			}
			for (var i = 0; i < paramArr.length; i++) {
				if (paramArr[i].indexOf("status") != -1) {
					let status = paramArr[i].split("=")[1]
					this.currentTab = this.tabs.filter(el => {
						return el.value == status
					})[0]
				}
			}
			this.getList()
		},
		onReady() {

		},
		watch: {

		},
		methods: {
			openReason(item) {
				this.currentProject = item
				this.showMask = true
				this.showReason = true
			},
			toNext(e) {
				this.currentPage = e
				this.getList()
			},
			toHandle(item, status) {
				let _this = this
				let msg = ""
				if (status == "running") {
					msg = "当前审核项目：" + item.name + "，是否确认通过审核？"
				} else {
					msg = "当前审核项目：" + item.name + "，是否确认拒绝该项目？"
				}
				uni.showModal({
					title: "项目审核",
					content: msg,
					confirmText: status == "running" ? "通过" : "拒绝",
					success(resp) {
						if (resp.confirm) {
							let url = "/admin/project/" + item.id + "/review"
							let data = {
								"status": status,
								"reason": _this.refuseReason
							}
							_this.$request(url, data, "POST").then(res => {
								if (res.code == 0) {
									uni.showModal({
										title: status == "running" ? "已通过" : "已拒绝",
										showCancel: false
									})
									_this.refuseReason = ""
									_this.getList()
									_this.close()
								}
							})
						}
					}
				})
			},
			save() {
				let _this = this
				console.log(this.$refs.painter)
				this.$refs.painter.canvasToTempFilePathSync({
					fileType: "jpg",
					// 如果返回的是base64是无法使用 saveImageToPhotosAlbum，需要设置 pathType为url
					pathType: 'url',
					quality: 1,
					success: (res) => {
						console.log(res.tempFilePath);
						let fileName = _this.currentProject.name + "_" + _this.currentProject.code + "_" + "海报"
						saveAs(res.tempFilePath, fileName);
						// _this.loadimg("project_poster", res.tempFilePath)
					},
					fail: (err) => {
						console.log(err, "err")
					}
				})
			},
			async loadimg(type, filePath) {
				var _this = this;
				let header = {
					"Authorization": "bearer " + uni.getStorageSync("token"),
					"accept": "application/json"
				}
				return new Promise((resolve, reject) => {
					uni.uploadFile({
						url: app.globalData.baseUrl + "/admin/upload/image?type=" + type,
						filePath: filePath,
						name: "file",
						header: header,
						success(resp) {
							var response = JSON.parse(resp.data);
							if (response.code == 0) {
								// window.open(response.data.url)
								saveAs(response.data.url, "海报");
							} else {
								uni.showModal({
									title: "上传失败",
									content: response.msg
								})
							}
						},
						complete() {
							uni.hideLoading();
						}
					})
					resolve(_this.reponsefiles)
				})
			},
			creatCode(item) {
				this.currentProject = item
				this.creatHaibao()
			},
			changeTab(item) {
				this.currentTab = item
				this.setPageName({
					pageName: "project_list"
				}, "", item.value)
				this.getList()
			},
			navigate(obj, id) {
				this.setPageName(obj, id)
				this.$emit("getPageName", obj)
			},
			getList() {
				uni.showLoading({
					title: "努力加载中~"
				})
				let url;
				if (this.currentTab.value == "all") {
					// 全部
					url = "/admin/projects?page=" + this.currentPage + "&create_time_start=" + this.searStartTime +
						"&create_time_end=" + this.searEndTime + "&keyword=" + this.searPro
				} else {
					url = "/admin/projects/" + this.currentTab.value + "?page=" + this.currentPage +
						"&create_time_start=" + this.searStartTime + "&create_time_end=" + this.searEndTime + "&keyword=" +
						this.searPro
				}
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.list = res.data.list
						this.pagination = res.data.pagination
					}
				})
			},
			handleSearch(e) {
				this.searPro = e.projectName
				this.searStartTime = e.startTime
				this.searEndTime = e.endTime
				this.getList()
			},
			close() {
				this.showMask = false
				this.showHaibao = false
				this.showReason = false
			},
			async creatHaibao() {
				uni.showLoading({
					title: "正在生成海报，请稍后..."
				})
				let codeImg = await this.getCode()
				if (codeImg) {
					uni.hideLoading()
					this.showMask = true
					this.showHaibao = true
					await this.viewHaibao(codeImg)
				}
			},
			endProject(item) {
				let _this = this
				let msg = "您当前准备关闭项目：" + item.name + "，项目关闭后不可恢复，请谨慎操作。是否确认关闭该项目？"
				uni.showModal({
					title: "提示",
					content: msg,
					confirmColor: "#f00",
					confirmText: "确认关闭",
					success(resp) {
						if (resp.confirm) {
							let url = "/broker/project/" + item.id + "/finish"
							_this.$request(url, {}, "POST").then(res => {
								if (res.code == 0) {
									uni.showModal({
										title: "项目已关闭",
										showCancel: false
									})
									_this.getList()
								}
							})
						}
					}
				})
			},
			submit(item) {
				let _this = this
				let url = "/broker/project/" + item.id + "/submit-review"
				uni.showModal({
					title: "提示",
					content: "您当前正在提审项目：" + item.name + "，是否确认提审该项目？",
					confirmText: "确认提审",
					success(resp) {
						if (resp.confirm) {
							_this.$request(url, {}, "POST").then(res => {
								if (res.code == 0) {
									uni.showModal({
										title: "提审成功",
										content: "项目已提审，请耐心等待平台审核。",
										showCancel: false
									})
									_this.getList()
								}
							})
						}
					}
				})
			},
			deleteProject(item) {
				let _this = this
				let url = "/broker/project/" + item.id
				uni.showModal({
					title: "提示",
					content: "您当前正在删除项目：" + item.name + "，是否确认删除该项目？",
					confirmText: "确认删除",
					confirmColor: "#f00",
					success(resp) {
						if (resp.confirm) {
							_this.$request(url, {}, "DELETE").then(res => {
								if (res.code == 0) {
									uni.showModal({
										title: "项目删除成功",
										showCancel: false
									})
									_this.getList()
								}
							})
						}
					}
				})
			},
			async getCode() {
				let url = "/admin/project/" + this.currentProject.id + "/qrcode/recruitment"
				let res = await this.$request(url, {}, "POST")
				if (res.code == 0) {
					return res.data.qrcode
				}
			},
			async viewHaibao(imgUrl) {
				let _this = this
				console.log(this.currentProject)
				this.poster.views = [{
						type: 'image',
						src: app.globalData.baseImageUrl + '/broker/toc_haibao.png?time=' + new Date()
							.getTime(),
						css: {
							width: '100%',
							margin: '0 auto'
						}
					},
					{
						type: "view",
						css: {
							width: '90%',
							position: 'absolute',
							bottom: '20px',
							left: '50%',
							transform: 'translateX(-50%)',
						},
						views: [{
								type: 'image',
								src: imgUrl,
								css: {
									width: '160px',
									height: "160px",
									borderRadius: "50%",
									display: "inline-block",
								}
							},
							{
								type: 'text',
								text: "扫码报名",
								css: {
									width: '60%',
									display: "inline-block",
									textAlign: 'right',
									lineHeight: "160px",
									color: "#fff",
									fontSize: "40px",
									fontWeight: 'bold'
								}
							},
						]
					},
					{
						type: 'text',
						text: this.currentProject.name,
						css: {
							// 设置居中对齐
							width: "30%",
							position: 'absolute',
							top: '35%',
							left: '60%',
							fontSize: "32px",
							color: "#000000",
							lineClamp: 2,
							fontWeight: "bold"
						}
					},
					{
						type: 'view',
						css: {
							// 设置居中对齐
							width: "80%",
							textAlign: 'left',
							position: 'absolute',
							top: '57%',
							left: '50%',
							transform: 'translateX(-50%)',
							lineHeight: '60px',
							lineClamp: 1,
						},
						views: [{
								type: "view",
								css: {
									width: "100%",
									display: "block",
									borderBottom: "1px dashed #FFAC3F"
								},
								views: [{
										type: "text",
										text: "工作名称：",
										css: {
											lineClamp: 1,
											display: "inline-block",
											color: "#3D3D3D",
											fontSize: "28px",
											marginRight: "8px",
										}
									},
									{
										type: "text",
										text: _this.currentProject.name,
										css: {
											lineClamp: 1,
											display: "inline-block",
											color: "#000",
											fontSize: "32px",
										}
									}
								]
							},
							{
								type: "view",
								css: {
									display: "block",
									borderBottom: "1px dashed #FFAC3F"
								},
								views: [{
										type: "text",
										text: "工作酬劳：",
										css: {
											display: "inline-block",
											color: "#3D3D3D",
											fontSize: "28px",
											marginRight: "8px",
										}
									},
									{
										type: "text",
										text: _this.currentProject.worker_salary,
										css: {
											display: "inline-block",
											color: "#FF4800",
											fontSize: "36px",
											fontWeight: "bold",
										}
									}
								]
							},
						]
					},

				]
			}
		}
	}
</script>

<style lang="scss" scoped>
	.table_wrap {

		// height:500px;
		.tips {
			margin-bottom: 20px;

			view {
				margin-right: 15px;
				font-size: 14px;

				text {
					color: #0092ff;
				}
			}

			.tip {
				color: #f00;
			}
		}
	}

	.mask {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
		z-index: 100;

		.inner {
			width: 530px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			padding: 15px 15px 50px 15px;
			border-radius: 10px;
			background: #fff;

			.cont {
				margin: 30px 0 50px 0;

				.tit {
					width: 80px;
				}

				.input_wrap {
					width: calc(100% - 80px);
					border: 1px solid #ccc;
					border-radius: 4px;
					position: relative;

					input {
						height: 40px;
						line-height: 40px;
						padding-left: 10px;
						box-sizing: border-box;
					}

					.period {
						position: absolute;
						top: 50%;
						right: 15px;
						transform: translateY(-50%);
						font-size: 14px;
					}
				}
			}

			.btns {
				margin-bottom: 15px;

				.btn {
					position: relative;
					left: 0;
					top: 0;
					border: 1px solid $base-color;
					margin-left: 15px;
				}

				.close {
					background: transparent;
					color: $base-color;
				}
			}
		}
	}
</style>